body {
  margin: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB );
  background-size: 400% 400%;
  -webkit-animation: walk 10s ease-in-out infinite;
          animation: walk 10s ease-in-out infinite;
}
@-webkit-keyframes walk{
  0%{
    background-position: 0 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0 50%;
  }
}
@keyframes walk{
  0%{
    background-position: 0 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0 50%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}

.title{
  text-align: center;
}

.container{
  width: 60%;
  margin: 10px auto;
}

.listItems{
  background: white;
  color: #46529d;
}

.listItems p{
  margin: 0;
  text-align: center;
  padding: 10px 0px;
}


.listItems span{
  display: inline-block;
  text-align: center;
  padding: 10px 0px;
  font-size: 20px;
  border: 1px solid white;
}
.listItems span.title{
  background: #2ebaee;
  color: white;
}

.listItems span.name, form input[id="name"]{
  width: 60%;
}

.listItems span.rank, form input[id="rank"]{
  width: 30%;
}

.listItems span.action, form input[type="submit"]{
  width: 10%;
}

.listItems span.icon{
  font-size: 25px;
  transform: 0.5s;
  color: red;
  cursor: pointer;
  font-weight: 700;
}

form input{
  height: 50px;
  padding: 10px;
  background: #f0f7f7;
  border: 10px solid white;
  border: transparent;
}

form input[type="submit"]{
  background: #2ebaee;
  border: 1px solid #2ebaee;
  color: white;
  cursor: pointer;
  font-size: 15px;
}

#restore{
  width: 80px;
  margin: 0;
  padding: 5px;
  font-size: 20px;
  transition: 0.5s;
  cursor: pointer;
}

#restore:hover{
  transition: 0.5s;
  font-size: 25px;
}

@media (max-width: 990px){
  .container{
    width: 100%;
    margin: 13px auto;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



